import { useLocation } from "react-router-dom";
import { useEffect } from "react";
import PageSeo from "@/components/seo/PageSeo";
import { Link } from "react-router-dom";

const NotFound = () => {
  const location = useLocation();

  useEffect(() => {
    console.error("404 Error: User attempted to access non-existent route:", location.pathname);
  }, [location.pathname]);

  return (
    <>
      <PageSeo
        title="Page Not Found | Pamod Madubashana"
        description="The page you are looking for does not exist on pamod.is-a.dev."
        path={location.pathname || "/404"}
        noIndex
      />
      <main className="flex min-h-screen items-center justify-center bg-muted" id="main-content">
        <section className="text-center">
          <h1 className="mb-4 text-4xl font-bold">404</h1>
          <p className="mb-4 text-xl text-muted-foreground">Oops! Page not found</p>
          <Link to="/" className="text-primary underline hover:text-primary/90">
            Return to Home
          </Link>
        </section>
      </main>
    </>
  );
};

export default NotFound;
